import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

const register = () => import( '@/components/register' );

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: index,
      alias: '/index'
    }
    ,{
      path: '/register/:id?',
      name: 'register',
      component: register,
      meta: {
        requireAuth: true
      }
    }
  ]
})



