<template>
  <div class="toast-success">
        <div class="toast-panel">
            <Icon type="ios-close-outline" :size="iconSize" :color="iconColor"></Icon>
            <div :style="{ fontSize: textSize+'px', color: iconColor, marginTop: '10px'}">
                {{iconMsg}}
            </div>
        </div>
  </div>
</template>
<script>
import comMinx from './share'
export default {
    data () {
        return {
            iconMsg: '错误',
            iconSize: 60, 
            iconColor: '#f00',
            textSize: 16,
           
        }
    },
    mixins: [comMinx],
    props: ["toastConfig"],
    mounted() {
            this.setMsg(); 
    },
    computed: { 
        
    },
    methods: {
        setMsg () {           
            if (this.isObject(this.toastConfig) === true)
            {
                this.iconMsg = this.toastConfig.msg || this.iconMsg;
                this.iconSize = this.toastConfig.size || this.iconSize;
                this.iconColor = this.toastConfig.color || this.textSize;
                this.textSize = this.toastConfig.textSize || this.textSize;

            }
        }
    }
}
</script>

<style>

.toast-success{
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    z-index: 9000;  
}
.toast-panel{
    position: relative;
    width: 120px;    
    top: 50%;  
    margin: auto;
    transform: translateY(-50%);
    margin: 0 auto;
    padding: 10px;
    border-radius: 8px;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.8);
    color: #ffffff;
    text-align: center;
}

</style>





